<template>
  <div>
    <div class="hello" ref="chartdiv">
    </div>
    <div id="myModal" class="modal">
      <div class="modal-content">
        <span class="close">&times;</span>
        <div id="content-div"></div>
      </div>
    </div>
  </div>
</template>

<script>
/* eslint-disable */
import * as am4core from "@amcharts/amcharts4/core";
// import * as am4charts from "@amcharts/amcharts4/charts";
import * as am4maps from "@amcharts/amcharts4/maps";
import am4themes_animated from "@amcharts/amcharts4/themes/animated";
import am4themes_frozen from "@amcharts/amcharts4/themes/frozen";
import am4geodata from "@amcharts/amcharts4-geodata/worldLow";
// import { add } from '@amcharts/amcharts4/.internal/core/utils/Array';

am4core.useTheme(am4themes_frozen);
am4core.useTheme(am4themes_animated);

export default {
  name: 'HelloWorld',
  methods: {
  },
  mounted() {
    let chart = am4core.create(this.$refs.chartdiv, am4maps.MapChart);
    var modal = document.getElementById("myModal");
    var span = document.getElementsByClassName("close")[0];

    span.onclick = function() {
      modal.style.display = "none";
    }

    chart.geodata = am4geodata;
    chart.projection = new am4maps.projections.Mercator();
    chart.homeZoomLevel = 3;
    chart.homeGeoPoint = {
        latitude: 38,
        longitude: -40
    };

    chart.colors.list = [
      am4core.color("#dadada"),   // grey
      am4core.color("#204969"),   // navy
      am4core.color("#08ffc8"),   // light green
      am4core.color("#fff7f7")    // white
    ];

    // Create map polygon series
    var polygonSeries = chart.series.push(new am4maps.MapPolygonSeries());
    polygonSeries.useGeodata = true;
    polygonSeries.mapPolygons.template.fill = chart.colors.getIndex(0).lighten(0.5);
    // Create hover state and set alternative fill color
    var hs =  polygonSeries.mapPolygons.template.states.create("hover");
    // hs.properties.fill = chart.colors.getIndex(0).lighten(0.6);
    polygonSeries.mapPolygons.template.nonScalingStroke = true;
    polygonSeries.exclude = ["AQ"];
    polygonSeries.data = [{
      "id": "US",
      "name": "United States",
      "value": 100,
      "fill": chart.colors.getIndex(0).brighten(-0.3)
    }, {
      "id": "ES",
      "name": "Spain",
      "value": 50,
      "fill": chart.colors.getIndex(0).brighten(-0.3)
    }];

    polygonSeries.mapPolygons.template.propertyFields.fill = "fill";

    // Add line bullets
    var cities = chart.series.push(new am4maps.MapImageSeries());
    cities.mapImages.template.nonScaling = true;

    var city = cities.mapImages.template.createChild(am4core.Circle);
    city.radius = 13;
    city.fill = chart.colors.getIndex(1).lighten(0.25);
    city.strokeWidth = 2;
    city.stroke = chart.colors.getIndex(3);

    function getCityContent(title) {
      var content = "";
      var video = "";
      switch (title) {
        case 'Chicago':
          content = 'The hometown of the Petersons. From meeting at Soldier Field to getting married at Rockefellow Memorial Chapel, August 18 in Chicago has created great memories for both of us.'
          video = `<iframe width="560" height="315" src="https://www.youtube.com/embed/ZAZE6PfLV0s?controls=0" frameborder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowfullscreen></iframe>`
          break;
        case 'Barcelona':
          content = 'Our first stop in Spain! We got to dive into two of our favorite things, sports and food. Here we are learning to make paella. Followed by a snap of our tour at the FC Barcelona Stadium and a clip at a RCD Espanyol futbol match.'
          video = '<video width="320" height="240" controls><source src="../assets/rcd.mp4" type="video/mov">Your browser does not support the video tag.</video>'
          break;
        default:
          content = 'Currently visiting...content coming soon';
          break;
      }
      return {'content':content,'video':video}
    }

    function addCity(coords, title, fill) {
      var city = cities.mapImages.create();
      var cityContent = getCityContent(title).content
      var video = getCityContent(title).video.length > 0 ? getCityContent(title).video : ''
      city.latitude = coords.latitude;
      city.longitude = coords.longitude;
      city.tooltipText = title;
      city.fill = fill;
      city.events.on("hit", function(ev) {
        console.log("clicked on ", ev.target)
        var modal = document.getElementById("myModal")
        var innerContent = document.getElementById("content-div")
        modal.style.display = "block"
        innerContent.innerHTML = `<h4>${title}</h4><div style="width: 25vw; margin:auto;"><h6>${cityContent}</h6></div>${video}`
        
      //   chart.openModal(`
      //     <div>
      //       <h4>${title}</h4>
      //       <div style="width: 25vw; margin:auto;"><h6>${cityContent}</h6></div>
      //       ${video}
      //     </div>
          
      //   `);
      }, this);
      return city;
    }

    // chart.modal.events.on("opened", function(ev) {
    //   console.log(ev);
    // });

    var chicago = addCity({ "latitude": 41.8781, "longitude": -87.6298 }, "Chicago", chart.colors.getIndex(1));
    var barcelona = addCity({ "latitude": 41.3851, "longitude": 2.1734 }, "Barcelona", chart.colors.getIndex(2));
    var ibiza = addCity({ "latitude": 39.0200, "longitude": 1.4821 }, "Ibiza", chart.colors.getIndex(2));
    var madrid = addCity({ "latitude": 40.4168, "longitude": -3.7038 }, "Madrid", chart.colors.getIndex(2));
    // var paris = addCity({ "latitude": 48.8567, "longitude": 2.3510 },"Paris");
    // var toronto = addCity({ "latitude": 43.8163, "longitude": -79.4287 }, "Toronto");
    // var la = addCity({ "latitude": 34.3, "longitude": -118.15 }, "Los Angeles");
    // var havana = addCity({ "latitude": 23, "longitude": -82 }, "Havana");

    // Add lines
    var lineSeries = chart.series.push(new am4maps.MapArcSeries());
    lineSeries.mapLines.template.line.strokeWidth = 2;
    lineSeries.mapLines.template.line.strokeOpacity = 0.5;
    lineSeries.mapLines.template.line.stroke = city.fill;
    lineSeries.mapLines.template.line.nonScalingStroke = true;
    lineSeries.mapLines.template.line.strokeDasharray = "1,1";
    lineSeries.zIndex = 10;

    var shadowLineSeries = chart.series.push(new am4maps.MapLineSeries());
    shadowLineSeries.mapLines.template.line.strokeOpacity = 0;
    shadowLineSeries.mapLines.template.line.nonScalingStroke = true;
    shadowLineSeries.mapLines.template.shortestDistance = false;
    shadowLineSeries.zIndex = 5;

    function addLine(from, to) {
        var line = lineSeries.mapLines.create();
        line.imagesToConnect = [from, to];
        line.line.controlPointDistance = -0.3;

        var shadowLine = shadowLineSeries.mapLines.create();
        shadowLine.imagesToConnect = [from, to];

        return line;
    }

    addLine(chicago,barcelona);
    addLine(barcelona,ibiza);
    addLine(ibiza,madrid);
    // addLine(madrid,barcelona);
    // addLine(paris, toronto);
    // addLine(toronto, la);
    // addLine(la, havana);

    // Add plane
    var plane = lineSeries.mapLines.getIndex(0).lineObjects.create();
    plane.position = 0;
    plane.width = 48;
    plane.height = 48;

    plane.adapter.add("scale", function(scale, target) {
        return 0.5 * (1 - (Math.abs(0.5 - target.position)));
    })

    var planeImage = plane.createChild(am4core.Sprite);
    planeImage.scale = 0.15;
    planeImage.horizontalCenter = "middle";
    planeImage.verticalCenter = "middle";
    planeImage.path = "m2,106h28l24,30h72l-44,-133h35l80,132h98c21,0 21,34 0,34l-98,0 -80,134h-35l43,-133h-71l-24,30h-28l15,-47";
    planeImage.fill = am4core.color("#000");
    planeImage.strokeOpacity = 0;

    var shadowPlane = shadowLineSeries.mapLines.getIndex(0).lineObjects.create();
    shadowPlane.position = 0;
    shadowPlane.width = 48;
    shadowPlane.height = 48;

    var shadowPlaneImage = shadowPlane.createChild(am4core.Sprite);
    shadowPlaneImage.scale = 0.05;
    shadowPlaneImage.horizontalCenter = "middle";
    shadowPlaneImage.verticalCenter = "middle";
    shadowPlaneImage.path = "m2,106h28l24,30h72l-44,-133h35l80,132h98c21,0 21,34 0,34l-98,0 -80,134h-35l43,-133h-71l-24,30h-28l15,-47";
    shadowPlaneImage.fill = am4core.color("#000");
    shadowPlaneImage.strokeOpacity = 0;

    shadowPlane.adapter.add("scale", function(scale, target) {
        target.opacity = (0.6 - (Math.abs(0.5 - target.position)));
        return 0.5 - 0.3 * (1 - (Math.abs(0.5 - target.position)));
    })

    // Plane animation
    var currentLine = 0;
    var direction = 1;
    function flyPlane() {
      // Get current line to attach plane to
      plane.mapLine = lineSeries.mapLines.getIndex(currentLine);
      plane.parent = lineSeries;
      shadowPlane.mapLine = shadowLineSeries.mapLines.getIndex(currentLine);
      shadowPlane.parent = shadowLineSeries;
      shadowPlaneImage.rotation = planeImage.rotation;

      // Set up animation
      var from, to;
      var numLines = lineSeries.mapLines.length;
      if (direction == 1) {
          from = 0
          to = 1;
          if (planeImage.rotation != 0) {
              planeImage.animate({ to: 0, property: "rotation" }, 1000).events.on("animationended", flyPlane);
              return;
          }
      }
      else {
          from = 1;
          to = 0;
          if (planeImage.rotation != 180) {
              planeImage.animate({ to: 180, property: "rotation" }, 1000).events.on("animationended", flyPlane);
              return;
          }
      }

      // Start the animation
      var animation = plane.animate({
          from: from,
          to: to,
          property: "position"
      }, 5000, am4core.ease.sinInOut);
      animation.events.on("animationended", flyPlane)
      /*animation.events.on("animationprogress", function(ev) {
        var progress = Math.abs(ev.progress - 0.5);
        //console.log(progress);
        //planeImage.scale += 0.2;
      });*/

      shadowPlane.animate({
          from: from,
          to: to,
          property: "position"
      }, 5000, am4core.ease.sinInOut);

      // Increment line, or reverse the direction
      currentLine += direction;
      if (currentLine < 0) {
          currentLine = 0;
          direction = 1;
      }
      else if ((currentLine + 1) > numLines) {
          currentLine = numLines - 1;
          direction = -1;
      }
    }

    // Go!
    flyPlane();

    window.onclick = function(event) {
      if (event.target == modal) {
        modal.style.display = "none";
      }
    }
  },

  beforeDestroy() {
    if (this.chart) {
      this.chart.dispose();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
.hello {
  width: 100%;
  height: 90vh;
}

/* The Modal (background) */
.modal {
  display: none; /* Hidden by default */
  position: fixed; /* Stay in place */
  z-index: 1; /* Sit on top */
  left: 0;
  top: 0;
  width: 100%; /* Full width */
  height: 100%; /* Full height */
  overflow: auto; /* Enable scroll if needed */
  background-color: rgb(0,0,0); /* Fallback color */
  background-color: rgba(0,0,0,0.4); /* Black w/ opacity */
}

/* Modal Content/Box */
.modal-content {
  background-color: #fefefe;
  margin: 15% auto; /* 15% from the top and centered */
  padding: 20px;
  border: 1px solid #888;
  width: 80%; /* Could be more or less, depending on screen size */
}

/* The Close Button */
.close {
  color: #aaa;
  float: right;
  font-size: 28px;
  font-weight: bold;
}

.close:hover,
.close:focus {
  color: black;
  text-decoration: none;
  cursor: pointer;
}
</style>



